@keyframes photo-animation {
  100% {
    transform: scale(1.1);
  }
}

@keyframes image-animation {
  100% {
    border-radius: 60px;
  }
}

@keyframes image-hover-out {
  0% {
    border-radius: 60px;
    transform: scale(1.1);
  }

  50% {
    border-radius: 60px;
    transform: scale(1.1);
  }

  100% {
    border-radius: 200px;
    transform: scale(1);
  }
}

.people {
  --columns: 2;
  grid-template-columns: repeat(var(--columns), 1fr);
  justify-items: center;
  gap: 40px;
  padding: 30px 35px 0;
  display: grid;
}

@media (width >= 1000px) {
  .people {
    --columns: 4;
  }
}

@media (width >= 750px) {
  .people {
    padding: 30px 160px 0;
  }
}

@media (width >= 1000px) {
  .people {
    padding: 30px 55px 0;
  }
}

@media (width >= 1500px) {
  .people {
    padding: 30px 305px 0;
  }
}

.person {
  text-align: center;
  border-radius: 10px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px 10px 16px;
  display: flex;
}

.person__photo {
  margin-bottom: 8px;
  position: relative;
}

.person__image {
  border-radius: 100%;
  width: 200px;
}

.person__rank {
  text-align: center;
  color: #8b0000;
  background-color: #fff;
  border-radius: 200px;
  width: 32px;
  height: 32px;
  font-family: Arial;
  font-size: 16px;
  font-weight: bold;
  line-height: 32px;
  position: absolute;
  top: 50%;
  transform: translate(-40px, -50%);
}

.person__name {
  color: #2f4f4f;
  font-family: Arial;
  font-size: 20px;
  font-weight: bold;
  line-height: 23px;
}

.person__name--link {
  text-decoration: none;
}

.person__extra {
  visibility: hidden;
  color: #000;
  margin-top: 9px;
  font-family: Arial;
  font-size: 18px;
  line-height: 20.7px;
}

.person:hover {
  background-color: #eee;
}

.person:hover .person__extra {
  visibility: visible;
}

.person:hover .person__photo {
  animation-name: photo-animation;
  animation-duration: 1s;
  animation-delay: .3s;
  animation-fill-mode: forwards;
}

.person:hover .person__image {
  animation-name: image-animation;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

.person:not(:hover) .person__image {
  animation-name: image-hover-out;
  animation-duration: 1s;
  animation-fill-mode: forwards;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
