@keyframes photo-animation {
	100% {
		transform: scale(110%);
	}
}
@keyframes image-animation {
	100% {
		border-radius: 60px;
	}
}
@keyframes image-hover-out {
	0% {
		transform: scale(110%);
		border-radius: 60px;
	}
	50% {
		transform: scale(110%);
		border-radius: 60px;
	}
	100% {
		transform: scale(100%);
		border-radius: 200px;
	}
}
